import classNames from 'classnames';
import React, {useEffect, useRef} from 'react';
import ReactDOM from 'react-dom';
import {ClassNameElement} from "../types";

interface ModalProps extends ClassNameElement {
  title?: string;
  children: React.ReactNode;
  onClose: () => void;
}

const ESC_CODE = 27;

const Modal: React.FC<ModalProps> = ({title, children, onClose, className}) => {
  const modalRef = useRef(null);
  const modalWrap = useRef(null);

  if (!document.querySelector('#app-modals')) {
    const container = document.createElement('div');
    container.id = 'app-modal';
    document.body.appendChild(container);
  }

  useEffect(() => {
    const escHandler = (event: any) => {
      if (event.keyCode === ESC_CODE) {
        onClose();
      }
    };

    document.addEventListener('keydown', escHandler, false);
    return () => {
      document.removeEventListener('keydown', escHandler, false);
    };
  }, [onClose]);

  useEffect(() => {
    const clickOutsideHandler = (event: any) => {
      if (modalWrap.current && modalWrap.current === event.target) {
        onClose();
      }
    };

    document.addEventListener('mousedown', clickOutsideHandler);
    return () => {
      document.removeEventListener('mousedown', clickOutsideHandler);
    };
  }, [modalRef, onClose]);

  useEffect(() => {
    document.body.style.overflow = 'hidden';

    return () => {
      document.body.style.overflow = 'visible';
    }
  })

  return ReactDOM.createPortal((
      <div className="Modal__background">
        <div className="Modal__wrap" ref={modalWrap}>
          <div className="Modal" ref={modalRef}>
            {title && <h3 className="Modal__title">{title}</h3>}
            <button className="Modal__close" onClick={() => {
              onClose()
            }}/>
            <div className={classNames("Modal__content", className)}>{children}</div>
          </div>
        </div>
      </div>
    ),
    document.querySelector('#app-modal')!);
};

export {Modal};