import React, {memo, ReactElement, useState} from "react";
import classNames from "classnames";
import {ChartDataProps, ClassNameElement} from "../types";
import {Modal} from "../Modal";
import {Button} from "../Elements/Button";

interface ChartDescProps extends ClassNameElement {
  chartData: ChartDataProps,
  measurement: string,
  active: number,
  withButton?: boolean,
  modalContent?: ReactElement,
}

const ChartDesc = memo(({className, chartData, measurement, active, withButton = false, modalContent}: ChartDescProps) => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);

  const isLastPreLast = active >= chartData.length - 2;

  const showValueChange = () => {
    if (isLastPreLast) {
      return Math.round((chartData[chartData.length - 1].value - chartData[chartData.length - 2].value) * 10) / 10;
    }

    return Math.round((chartData[chartData.length - 1].value - chartData[active].value) * 10) / 10;
  };

  const showShiftChange = () => (Math.round(showValueChange() / (chartData[active].value / 100) * 10) / 10).toFixed(1);

  return (
    <div className={classNames('chartDesc', className)}>
      <p className="chartDesc__title">Изменение <br/> к отчетному году</p>
      <b className="chartDesc__shift">{showShiftChange()}%</b>
      <b className="chartDesc__value">{showValueChange().toFixed(1)}</b>
      <span className="chartDesc__measurement">{measurement}</span>
      {withButton && <Button
          className='chartDesc__button'
          onClick={() => {setIsModalOpen(true)}}>Подробнее</Button>}
      {isModalOpen && <Modal className="chartDesc__modal" onClose={() => {setIsModalOpen(false);}}>
        {modalContent}
      </Modal>}
    </div>
  )
});

export {ChartDesc};