import React, {memo, useRef} from "react";
import ReactEcharts, {EChartsOption} from "echarts-for-react";
import {ClassNameElement} from "../types";
import classNames from "classnames";
import {Button} from "../Elements/Button";
import {PieChartDataProps} from "../../pages/GasPage/components/SectionProduction/components/PieChartProduction";
import {scroller} from "react-scroll";
import {useDispatch} from "react-redux";

interface PieChartProps extends ClassNameElement {
  active: number,
  setActive: (active: number) => void,
  chartData: PieChartDataProps[][],
  optionsStat: EChartsOption,
  title: string,
  desc?: string,
  type?: 'tabsLeft',
  button?: string,
  setOuterActive?: (num: number) => void,
  extraActive?: number,
}

const PieChart = memo(({
                         className,
                         active,
                         setActive,
                         chartData,
                         optionsStat,
                         title,
                         desc,
                         type,
                         button,
                         setOuterActive,
                         extraActive
                       }: PieChartProps) => {

  const myChart = useRef(null as any);

  const dispatch = useDispatch();

  const setLabelValue = (value: number) => {
    if (/\./.test(`${value}`)) {
      return value;
    }

    return `${value}.0`;
  }

  return (
    <div className={classNames('pieChart', className, type && `pieChart__${type}`)}>
      <h4 className="pieChart__title">{title}</h4>
      <div className="pieChart__content">
        <div className="pieChart__buttonBox">
          {chartData.map((item, i, arr) => (
            <Button
              key={arr[i][i].name}
              className={classNames('pieChart__button', {'active': active === i})}
              disabled={active === i}
              onClick={() => {
                if (myChart && myChart.current) {
                  myChart.current?.getEchartsInstance().setOption({
                      series: [
                        {...optionsStat, ...{data: chartData[i]}},
                      ],
                    }, true
                  );
                }
                setActive(i);
              }}>
              {arr[i][i].name}
            </Button>
          ))}
        </div>
        <div className="pieChart__chartWrapper">
          <div className="pieChart__chartBox">
            <p className="pieChart__label">{setLabelValue(chartData[active][active].value)}%</p>
            <ReactEcharts
              ref={myChart}
              className='pieChart__chart'
              option={{series: [{...optionsStat, ...{data: chartData[active]}}]}}/>
          </div>
        </div>
        <div className="pieChart__dataWrapper">
          <div>
            <span className='pieChart__dataHint'>Изменение к аналогичному периоду прошлого года</span>
            <p className="pieChart__dataShift">{chartData[active][active].shift}</p>
            <span className="pieChart__dataMeasurement">п.п.</span>
          </div>
          {desc && <p className="pieChart__dataDesc">{desc}</p>}
          {button && <Button className="pieChart__buttonDesc" onClick={() => {
            setOuterActive && dispatch(setOuterActive(extraActive ? 1 : 2));
            scroller.scrollTo('page__section--supply', {
              duration: 0,
              delay: 0,
              smooth: "easeInOutQuart",
            });
          }}>{button}</Button>}
        </div>
        {desc && <p className="pieChart__dataDesc pieChart__dataDesc--mobile">{desc}</p>}
      </div>
    </div>
  )
});

export {PieChart};