import {BarVertChart} from "../../../../../../components/BarVertChart";
import {
  BAR_CHART_DRILLING_TECH_FIGURES_HORIZONTAL,
  BAR_CHART_DRILLING_TECH_FIGURES_RECON,
  BAR_CHART_DRILLING_TECH_FIGURES_TOTAL, BAR_CHART_DRILLING_TECH_FIGURES_USAGE
} from "../../../../config/SectionTechFiguresOil/BarChartDrillingTechFigures";
import {Tabs} from "../../../../../../components/Elements/Tabs";
import { setTechFiguresDrillingOilTab } from "../../../../redux/OilPage.reducer";
import {getTechFiguresDrillingOilTab} from "../../../../redux/OilPage.selector";
import {useSelector} from "react-redux";

const tabs = [
  {
    name: 'Всего',
  },
  {
    name: 'Эксплуатационное бурение',
  },
  {
    name: 'Разведочное бурение',
  },
  {
    name: 'Горизонтальное бурение',
  },
];

export const BarChartDrillingTechFigures = () => {
  const activeDrillingTab = useSelector(getTechFiguresDrillingOilTab);

  const getCurrentData = (cur: number) => {
    switch (cur) {
      case 3:
        return BAR_CHART_DRILLING_TECH_FIGURES_HORIZONTAL;
      case 2:
        return BAR_CHART_DRILLING_TECH_FIGURES_RECON;
      case 1:
        return BAR_CHART_DRILLING_TECH_FIGURES_USAGE;
      case 0:
      default:
        return BAR_CHART_DRILLING_TECH_FIGURES_TOTAL;
    }
  }

  return (
    <Tabs
      active={activeDrillingTab}
      setActive={setTechFiguresDrillingOilTab}
      tabs={tabs}
      setDispatch
      className='page__leftItem mb-s-2'
    >
      <BarVertChart
        className='page__wide page__leftItem'
        chartData={getCurrentData(activeDrillingTab)}
        measurement='тыс. м'
      />
    </Tabs>
  );
}