import React, {memo, useEffect, useState} from "react";
import {BarChart} from "../../../../../../components/BarChart";
import { ChartDescPrev } from "../../../../../../components/ChartDescPrev";
import {ChartDataProps, ClassNameElement, PrevChartItemProps} from "../../../../../../components/types";
import classNames from "classnames";
import {useWindowWidth} from "../../../../../../hooks/hooks";

interface BarChartMiningProps extends ClassNameElement {
  chartData: PrevChartItemProps[],
  chartNames: string[],
  measurement: string,
  color: {
    current: string,
    regular: string,
  }
}

const BarChartMining = memo(({chartData, className, chartNames, measurement, color}: BarChartMiningProps) => {
  const [active, setActive] = useState(0);

  const [newData, setNewData] = useState<ChartDataProps>([]);

  useEffect(() => {
    setNewData(chartData.map((item, i) => {
      if (i === active) {
        item.itemStyle = {
          color: color.current,
        }
      } else {
        delete item.itemStyle;
      }

      return item;
    }));
  }, [active, chartData, color]);

  const chartConfig = {
    title: {
      subtext: measurement,
    },
    height: 550,
    animationDuration: 0,
    grid: {
      left: 180,
    },
    xAxis: {
      show: false,
    },
    yAxis: {
      type: 'category',
      data: chartNames,
      inverse: true,
      axisLine: {
        show: false,
      },
      axisLabel: {
        margin: 10,
        fontWeight: 700,
        fontSize: 15,
        color: '#000',
      },
      axisTick: {
        show: false,
      },
    },
    series: [
      {
        data: newData,
        label: {
          show: true,
          position: 'right',
          fontWeight: 700,
          fontSize: 15,
          color: '#000',
          formatter: '{c}',
        },
        type: 'bar',
        itemStyle: {
          color: color.regular,
        },
      }
    ],
  };

  const chartConfigMobile = {
    width: 210,
    height: 550,
    animationDuration: 0,
    grid: {
      left: 90,
    },
    xAxis: {
      show: false,
    },
    yAxis: {
      type: 'category',
      data: chartNames,
      inverse: true,
      axisLine: {
        show: false,
      },
      axisLabel: {
        margin: 10,
        fontWeight: 700,
        fontSize: 10,
        color: '#000',
      },
      axisTick: {
        show: false,
      },
    },
    series: [
      {
        data: newData,
        label: {
          show: true,
          position: 'right',
          fontWeight: 700,
          fontSize: 10,
          color: '#000',
          formatter: '{c}',
        },
        type: 'bar',
        itemStyle: {
          color: color.regular,
        },
      }
    ],
  };

  return (
    <div className={classNames("barChartMining", className)}>
      <BarChart
        setActive={setActive}
        height='600px'
        option={useWindowWidth() < 768 ? chartConfigMobile : chartConfig}
      />
      <ChartDescPrev
        chartData={chartData}
        active={active}
        title='Изменение к аналогичному периоду прошлого года'
        measurement={measurement}
      />
    </div>
  )
});

export {BarChartMining};