import React, {memo} from "react";
import classNames from "classnames";
import {ClassNameElement} from "../../types";

interface ButtonProps extends ClassNameElement {
  children: React.ReactNode | string,
  onClick: () => void,
  disabled?: boolean,
  type?: 'gray',
}

const Button = memo(({className, children, onClick, disabled, type}: ButtonProps) => {
  return (
    <button
      disabled={disabled}
      className={classNames('button', className, type)}
      onClick={onClick}
    >
      {children}
    </button>
  )
});

export {Button};