import React, {memo} from "react";
import {ClassNameElement} from "../../types";
import classNames from "classnames";

interface TitleProps extends ClassNameElement {
  value: string;
  type?: 'small';
  sub?: string;
}

const Heading = memo(({value, className, type, sub}: TitleProps) => {
  return (
    <div className={classNames("heading", className, type && type)}>
      <h4>{value}</h4>
      {sub && <p className='heading__sub'>{sub}</p>}
    </div>
  );
});

export {Heading};
