import React, {memo, useState} from "react";
import classNames from "classnames";
import {Heading} from "../../../../../../components/Elements/Heading";
import mapTransport from "../../../../../../images/OilExportMapTransport/OilExportMap.png";
import transportBig from "../../../../../../images/OilExportMapTransport/OilExportNumbersBig.svg";
import transportSmall from "../../../../../../images/OilExportMapTransport/OilExportNumbersSmall.svg";
import transportPercentage from "../../../../../../images/OilExportMapTransport/OilExportPercentage.png";
import {ClassNameElement} from "../../../../../../components/types";

interface OilExportTransportMapProps extends ClassNameElement {

}

export const OilExportTransportMap = memo(({className}: OilExportTransportMapProps) => {
  const [active, setActive] = useState<number>(0);

  return (
    <div className={classNames("OilExportTransportMap", className)}>
      <Heading
        className='OilExportTransportMap__title'
        value='Транспортные коридоры экспорта нефти из российской федерации'/>
      <img className='OilExportTransportMap__map' src={mapTransport} alt="Карта"/>
      <img className={classNames('OilExportTransportMap__mapPart OilExportTransportMap__mapPart--big', {'active': active === 0})} src={transportBig} alt="Карта график"/>
      <img className={classNames('OilExportTransportMap__mapPart OilExportTransportMap__mapPart--small', {'active': active === 1})} src={transportSmall} alt="Карта график"/>
      <img className={classNames('OilExportTransportMap__mapPart OilExportTransportMap__mapPart--percentage')} src={transportPercentage} alt="Карта график"/>
      <button className='OilExportTransportMap__toggle' onClick={() => {setActive(!active ? 1 : 0)}}/>
    </div>
  );
});