import React from 'react';
import {scroller} from 'react-scroll';
import {IconPoweredBy} from "../../icons/poweredBy/poweredBy";
import mapRussia from '../../images/mapRussia.jpg';
import {Output} from './components/Output';
import {outputExport, outputMining } from './config/output';
import classNames from "classnames";

const MainPage = () => {
  const scrollToSection = () => {
    scroller.scrollTo("main__section-title", {
      duration: 400,
      delay: 0,
      smooth: "easeInOutQuart",
    })
  };

  return (
    <section className='main'>
      <div className="main__upper">
        <div className='main__header-box'>
          <h1 className='main__header'>ТЭК</h1>
          <h1 className='main__subheader'>России</h1>
          <h2 className='main__desc'>функционирование и развитие</h2>
        </div>
        <span className='main__year'>20 20</span>
        <button className='main__btn-nav' onClick={scrollToSection}>Подробнее</button>
        <img className='main__map' src={mapRussia} alt="Карта России"/>
        <IconPoweredBy className='main__poweredBy'/>
      </div>
      <div className="main__lower">
        <h3 className='main__section-title'>Краткие итоги работы тэк россии</h3>
        <h4 className='main__section-subtitle'>Добыча/производство</h4>
        <ul className="main__section-list">
          {outputMining.map((item, i) => (
            <li key={`${i}${item.config[0].title}`} className={classNames("main__section-item", {'isMobile': item?.isMobOnly})}>
              <Output
                link={item.link}
              config={item.config.map((unit, idx) => (
                  {
                    title: unit.title,
                    value: unit.value,
                    measurement: unit.measurement,
                    isPositive: unit.isPositive,
                    percentage: unit.percentage,
                  }
                ))}/>
            </li>
          ))}
        </ul>
        <h4 className='main__section-subtitle'>Экспорт</h4>
        <ul className="main__section-list">
          {outputExport.map((item, i) => (
            <li key={`${i}${item.config[0].title}`} className="main__section-item">
              <Output
                link={item.link}
                config={item.config.map((unit, idx) => (
                  {
                    title: unit.title,
                    value: unit.value,
                    measurement: unit.measurement,
                    isPositive: unit.isPositive,
                    percentage: unit.percentage,
                  }
                ))}/>
            </li>
          ))}
        </ul>
      </div>
    </section>
  )
}

export {MainPage};