import React, {memo, useState} from "react";
import {BarChart} from "../../../BarChart";
import { BarDescField } from "../../../BarChart/components/BarDescField";
import {OverviewDataProps} from "../../StructureOverview";

interface BarChartFieldProps {
  chartData: OverviewDataProps[],
  height: number,
}

const BarChartField = memo(({chartData, height}: BarChartFieldProps) => {
  const [active, setActive] = useState(0);

  const chartNames = chartData.map(item => item.name);
  const chartValues = chartData.map(item => ({value: item.value, itemStyle: item.itemStyle}));
  const chartColors = chartData.map(item => item.itemStyle.color);

  return (
    <div className='barChartField'>
      <BarChart
        className='barChartField__chart'
        option={{
          height: height,
          animationDuration: 0,
          grid: {
            left: 150,
          },
          xAxis: {
            show: false,
          },
          yAxis: {
            type: 'category',
            data: chartNames,
            inverse: true,
            axisLine: {
              show: false,
            },
            axisLabel: {
              margin: 10,
              fontWeight: 700,
              fontSize: 15,
              color: '#000',
            },
            axisTick: {
              show: false,
            },
          },
          series: [
            {
              data: chartValues,
              label: {
                show: true,
                position: 'right',
                fontWeight: 700,
                fontSize: 15,
                color: '#000',
                formatter: '{c} %',
              },
              type: 'bar'
            }
          ],
        }}
        setActive={setActive}
      />
      <div className="barChartField__mobile">
        <div className="barChartField__mobile-buttonBox">
          {chartNames.map((item, i) => (
            <button key={`${item}${i}`} onClick={() => setActive(i)} style={{backgroundColor: chartValues[i].itemStyle.color}} className='barChartField__mobileButton'>{item}</button>
          ))}
        </div>
        <div className="barChartField__mobile-lineBox">
          <span className="barChartField__mobileLine" style={{width: `${chartValues[active].value}%`,
            backgroundColor: chartValues[active].itemStyle.color}}/>
          <span className="barChartField__mobileValue">{chartValues[active].value}%</span>
        </div>
      </div>
      <BarDescField
        active={active}
        colors={chartColors}
        chartData={chartData}
      />
    </div>
  );
});

export {BarChartField};