import React, {useCallback, useMemo} from 'react';
import {PATHS} from "../constants";
import {CONFIG, HEADER_WIDTH} from './constants/config';
import classNames from "classnames";
import {ClassNameElement} from "../../types";
import {scroller} from "react-scroll";
import {useDispatch} from "react-redux";

interface SubheaderProps extends ClassNameElement {
  pathname: string,
  sectionScroll: {
    [key: string]: number,
  },
}

const Subheader = ({pathname, className, sectionScroll}: SubheaderProps) => {
  const dispatch = useDispatch();

  const subheaderValues = (useMemo(() => () => {
    switch (pathname) {
      case PATHS.GAS:
        return CONFIG.GAS;
      case PATHS.OIL:
        return CONFIG.OIL;
      default:
        break;
    }
  }, [pathname]))();

  const scrollToPath = useCallback((path, offset = 0) => {
    scroller.scrollTo(`page__section--${path}`, {
      duration: 0,
      delay: 0,
      offset: offset,
      smooth: "easeInOutQuart",
    });
  }, []);

  return (
    <div className={classNames("subheader", className)}>
      <div className="subheader__wrapper">
        <ul className='subheader__content'>
          {subheaderValues?.length && subheaderValues.map((value, i, arr) => {
            const {nameShort, path, innerTabs} = value;

            return (
              <li key={nameShort}
                  className={classNames('subheader__value-wrapper', {'subheader__value-wrapper--short': innerTabs})}>
                <div className='subheader__value-box'>
                  <button
                    className={classNames('subheader__value', {'active': arr[i + 1]?.path ? sectionScroll[path] <= HEADER_WIDTH && sectionScroll[arr[i + 1]?.path] > HEADER_WIDTH : sectionScroll[path] <= HEADER_WIDTH})}
                    onClick={() => {
                      scrollToPath(path)
                    }}
                  >
                    {value.nameShort}
                  </button>
                </div>
                {innerTabs?.list?.map((item, i) => (
                  <button
                    key={item.value}
                    className={classNames('subheader__value-tab', {'subheader__value-tab--notTab': item.tabOrder === undefined})}
                    onClick={() => {
                      if (typeof item.tabOrder === 'number' && innerTabs.action) {
                        dispatch(innerTabs.action(item.tabOrder));
                      }

                      item.path ? scrollToPath(item.path, -250) : scrollToPath(path);
                    }}>
                    {item.value}
                  </button>
                ))}
              </li>
            )
          })}
        </ul>
      </div>
    </div>
  )
};

export {Subheader};
