import React, {memo, ReactElement, useMemo, useRef, useState} from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import * as echarts from 'echarts';
import classNames from "classnames";
import {ClassNameElement} from "../types";
import {ChartDescPrevMulti} from "../ChartDescPrevMulti";
import {useWindowWidth} from "../../hooks/hooks";
import {useSelector} from "react-redux";
import {getCurrentPath} from "../../redux/theme.selector";


interface LineChartProps extends ClassNameElement {
  chartData: prevChartDataMultiItem[],
  measurement: string,
  desc?: string,
  tooltipVals?: string[],
  activeTab: number,
  interval?: number,
  subtext?: string,
  noDesc?: boolean,
  withButton?: boolean,
  modalContent?: ReactElement,
}

export interface prevChartDataMultiItem {
  value: number,
  prev: number[],
}

type colorThemeType = {
  [key in string]: {
    prev: string,
    current: string,
  }
}

// TODO: replace refining and coal colors once available
const colorTheme: colorThemeType = {
  gas: {
    prev: '#71c5bc',
    current: '#1b3a98',
  },
  oil: {
    prev: '#F3AE4D',
    current: '#EA3742',
  },
  refining: {
    prev: '#71c5bc',
    current: '#1b3a98',
  },
  coal: {
    prev: '#71c5bc',
    current: '#1b3a98',
  },
}

const LineChart = memo(({
                          className,
                          chartData,
                          measurement,
                          desc,
                          tooltipVals = [],
                          activeTab,
                          interval = 250,
                          subtext,
                          noDesc = false,
                          withButton,
                          modalContent
                        }: LineChartProps) => {
  const [active, setActive] = useState(chartData.length - 1);

  const currentPath = useSelector(getCurrentPath);

  const minValueObj = chartData.reduce((acc, cur) => {
    return acc.value < cur.value ? acc : cur;
  });

  const maxValueObj = chartData.reduce((acc, cur) => {
    return acc.value > cur.value ? acc : cur;
  });


  const prevDataChart = useMemo(() => (
    chartData.map(item => {
      if (item.prev.length) {
        return ({value: item.prev[activeTab]});
      }

      return item;
    })), [chartData, activeTab]);

  const chartSeries: EChartsOption = [
    {
      data: chartData,
      animationDuration: 0,
      type: 'line',
      itemStyle: {
        color: currentPath ? colorTheme[currentPath].current : '#1b3a98',
      },
      selectionMode: false,
      symbol: 'circle',
      symbolSize: (value: any, params: any) => {
        if (chartData[active].value === value) {
          return 10;
        } else {
          return 0;
        }
      },
      label: {
        position: 'top',
        fontWeight: 700,
        fontSize: 20,
        color: '#000',
      },
    }
  ];

  if (tooltipVals?.length) {
    chartSeries.unshift({
      data: prevDataChart,
      animationDuration: 0,
      type: 'line',
      itemStyle: {
        color: currentPath ? colorTheme[currentPath].prev : '#71c5bc',
      },
      lineStyle: {
        width: 0
      },
      selectionMode: false,
      symbolSize: 0,
      areaStyle: {
        opacity: 0.8,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          {
            offset: 0,
            color: currentPath ? colorTheme[currentPath].prev : '#71c5bc',
          },
          {
            offset: 1,
            color: 'white'
          }
        ])
      },
    },)
  }

  const setChartFormatter = (e: EChartsOption) => {
    const labelValue = e[0]?.data?.value.toFixed(1);

    if (tooltipVals?.length && e?.length) {
      const prevLabelValue = e[1]?.data?.value.toFixed(1);

      if (currentPath === 'oil') {
        return `${tooltipVals[0]}: <b style="color: #EA3742">${prevLabelValue}</b> <br/> ${tooltipVals[activeTab + 1]}: <b style="color: #F3AE4D">${labelValue}</b>`;
      }

      return `${tooltipVals[0]}: <b style="color: #1B3A98">${prevLabelValue}</b> <br/> ${tooltipVals[activeTab + 1]}: <b style="color: #71c5bc">${labelValue}</b>`;
    }

    return labelValue;
  }

  const chartConfig = {
    title: {
      subtext: subtext ? subtext : '',
    },
    xAxis: {
      type: 'category',
      data: ['янв', 'фев', 'мар', 'апр', 'май', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек'],
      axisTick: {
        show: false,
      },
    },
    tooltip: {
      trigger: 'axis',
      formatter: (e: EChartsOption) => setChartFormatter(e),
    },
    animationDuration: 0,
    grid: {
      left: 50,
    },
    height: 350,
    yAxis: {
      type: 'value',
      min: Math.round(minValueObj.value / interval) * interval - interval > 0 ? Math.round(minValueObj.value / interval) * interval - interval : 0,
      max: Math.round(maxValueObj.value / interval) * interval + interval,
      interval: interval,
    },
    series: chartSeries,
  };

  const chartConfigMobile = {
    title: {
      subtext: subtext ? subtext : '',
    },
    xAxis: {
      type: 'category',
      data: ['янв', 'фев', 'мар', 'апр', 'май', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек'],
      axisTick: {
        show: false,
      },
      axisLabel: {
        fontSize: 8,
      },
    },
    tooltip: {
      trigger: 'axis',
      formatter: (e: EChartsOption) => setChartFormatter(e),
    },
    animationDuration: 0,
    grid: {
      left: 20,
    },
    width: 300,
    height: 100,
    yAxis: {
      type: 'value',
      min: Math.round(minValueObj.value / interval) * interval - interval > 0 ? Math.round(minValueObj.value / interval) * interval - interval : 0,
      max: Math.round(maxValueObj.value / interval) * interval + interval,
      interval: interval,
      axisLabel: {
        fontSize: 8,
      },
    },
    series: chartSeries,
  };

  const myChart = useRef(null as any);

  return (
    <div className={classNames('lineChart', className)}>
      <ReactEcharts
        ref={myChart}
        className='lineChart__chart'
        style={{
          height: useWindowWidth() < 768 ? '200px' : '500px',
        }}
        option={useWindowWidth() < 768 ? chartConfigMobile : chartConfig}
        onEvents={{
          'highlight': (params: EChartsOption) => {
            if (params?.batch?.length) {
              setActive(params?.batch[0]?.dataIndex);
            }
          },
        }}
      />
      {!noDesc && <ChartDescPrevMulti
          chartData={chartData}
          active={active}
          title='Изменение к отчетному году'
          measurement={measurement}
          desc={desc}
          activePrev={activeTab}
          withButton={withButton}
          modalContent={modalContent}
      />}
    </div>
  );
});

export {LineChart};