import React, {memo} from "react";
import {ROUTE} from "../Header/constants";
import {NavLink, useLocation} from "react-router-dom";
import classNames from "classnames";
import {IconPoweredBy} from "../../icons/poweredBy/poweredBy";

const Footer = memo(() => {
  const {pathname} = useLocation();

  return (
    <>
      {pathname !== '/' && (
        <footer className="footer">
          <div className="footer__content">
            <div className="footer__title-box">
              <h5 className="footer__title">ТЭК России</h5>
              <p className="footer__subtitle">Функционирование и развитие</p>
            </div>
            <div className="footer__nav-box">
              <p className="footer__nav-title footer__wide">Краткие итоги работы ТЭК России</p>
              <p className="footer__nav-title">Промышленность:</p>
              <ul className='footer__nav'>
                {ROUTE.map(route => (
                  <li className='footer__nav-item' key={route.path}>
                    <NavLink to={route.path}>
                      <p className={classNames('footer__nav-value', {active: pathname === route.path})}>{route.nameFooter}</p>
                    </NavLink>
                  </li>
                ))}
              </ul>
            </div>
            <IconPoweredBy className='footer__poweredBy footer__wide'/>
          </div>
        </footer>
      )}
    </>
  );
});

export {Footer};