import {configureStore} from "@reduxjs/toolkit";
import { gasPageSlice } from "./pages/GasPage/redux/GasPage.reducer";
import { oilPageSlice } from "./pages/OilPage/redux/OilPage.reducer";
import { themeSlice } from "./redux/theme.reducer";

export const store = configureStore({
  reducer: {
    theme: themeSlice.reducer,
    gasPage: gasPageSlice.reducer,
    oilPage: oilPageSlice.reducer,
  }
});

export type RootState = ReturnType<typeof store.getState>
