import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  path: '/',
};

export const themeSlice = createSlice({
  name: 'theme',
  initialState,
  reducers: {
    setCurrentPath: (state, action) => {
      state.path = action.payload;
    },
  },
});

export const {setCurrentPath} = themeSlice.actions;
