import React, {memo, useState} from "react";
import {ClassNameElement} from "../../../../../../components/types";
import {Heading} from "../../../../../../components/Elements/Heading";
import classNames from "classnames";
import mapTransport from "../../../../../../images/mapTransport.svg";
import transportBig from "../../../../../../images/MapTransport/transportBig.svg";
import transportSmall from "../../../../../../images/MapTransport/transportSmall.svg";
import transportPercentage from "../../../../../../images/MapTransport/mapPercentage.svg";

interface MapTransportProps extends ClassNameElement {

}

const MapTransport = memo(({className}: MapTransportProps) => {
  const [active, setActive] = useState<number>(0);

  return (
    <div className={classNames("mapTransport", className)}>
      <Heading
        className='mapTransport__title'
        value='Основные транспортные коридоры экспорта газа из Российской Федерации'/>
      <img className='mapTransport__map' src={mapTransport} alt="Карта"/>
      <img className={classNames('mapTransport__mapPart mapTransport__mapPart--big', {'active': active === 0})} src={transportBig} alt="Карта график"/>
      <img className={classNames('mapTransport__mapPart mapTransport__mapPart--small', {'active': active === 1})} src={transportSmall} alt="Карта график"/>
      <img className={classNames('mapTransport__mapPart mapTransport__mapPart--percentage')} src={transportPercentage} alt="Карта график"/>
      <button className='mapTransport__toggle' onClick={() => {setActive(!active ? 1 : 0)}}/>
    </div>
  )
});

export {MapTransport};