import React, {memo, useState} from "react";
import classNames from "classnames";
import {Tabs} from "../Elements/Tabs";
import {ClassNameElement} from "../types";

interface FieldOverviewProps extends ClassNameElement {
  background: string,
  title: string,
  subtitle: string,
  rateList: RateItem[],
  slides: SliderOverview[],
}

interface RateItem {
  title: string,
  value: string,
  measurement: string,
}

interface SliderOverview {
  text: string,
  list?: string[],
}

const FieldOverview = memo(({background, title, subtitle, rateList, slides, className}: FieldOverviewProps) => {
  const [slideNum, setSlideNum] = useState<number>(0);
  const [order, setOrder] = useState<number>(0);

  const changeSlide = (isForward = false) => {
    if (isForward) {
      slideNum === slides.length - 1 ? setSlideNum(0) : setSlideNum((prevSlide) => prevSlide + 1);
    } else {
      slideNum === 0 ? setSlideNum(slides.length - 1) : setSlideNum((prevSlide) => prevSlide - 1);
    }

  }

  return (
    <section className={classNames('fieldOverview', className)}>
      <img className='fieldOverview__bg' src={background} alt="backgroundImg"/>
      <div className="fieldOverview__content">
        <h1 className="fieldOverview__title">
          <p className="fieldOverview__title-main">{title}</p>
          <p className="fieldOverview__title-sub">{subtitle}</p>
        </h1>
        <div className="fieldOverview__inner">
          {<Tabs
            className='fieldOverview__toggle'
            tabs={
              [
                {
                  name: 'Показатели',
                },
                {
                  name: 'Тенденции и факторы',
                }
              ]
            }
            active={order}
            setActive={setOrder}
            withPrev
          >
            {order ? (
              <div className='fieldOverview__slider'>
                <button onClick={() => changeSlide()} className='fieldOverview__sliderButton'/>
                <button  onClick={() => changeSlide(true)} className='fieldOverview__sliderButton fieldOverview__sliderButton--next'/>
                <div className="fieldOverview__slider-toggle-box">
                  {slides.map((item, i, arr) => (
                    <button
                      key={item.text}
                      onClick={() => setSlideNum(i)}
                      className={classNames('fieldOverview__slider-toggle', {'active': slideNum === i})}
                    />
                  ))}
                  {slides.map((item, i) => (
                    <div key={`${item.text}${i}`}
                         className={classNames("fieldOverview__slider-list-wrapper", {'active': slideNum === i})}>
                      <p className="fieldOverview__slider-text">
                        {item.text}
                      </p>
                      {item?.list?.length && (
                        <ul className="fieldOverview__slider-list">
                          {item.list.map(el => (
                            <li
                              key={el}
                              className={classNames("fieldOverview__slider-list-item")}>
                              {el}
                            </li>
                          ))}
                        </ul>)}
                    </div>
                  ))}
                </div>
              </div>
            ) : (
              <ul className="fieldOverview__rate-list">
                {rateList.map(item => (
                  <li key={item.title} className="fieldOverview__rate-item">
                    <p className="fieldOverview__rate-title">{item.title}</p>
                    <b className="fieldOverview__rate-value">{item.value}</b>
                    <span className="fieldOverview__rate-measurement">{item.measurement}</span>
                  </li>
                ))}
              </ul>
            )}
          </Tabs>}
        </div>
      </div>
    </section>
  );
});

export {FieldOverview};