import React, {memo, ReactElement, useState} from "react";
import {prevChartDataMultiItem} from "../LineChart";
import {Button} from "../Elements/Button";
import {Modal} from "../Modal";

interface ChartDescPrevMultiProps {
  chartData: prevChartDataMultiItem[],
  active: number,
  title: string,
  measurement: string,
  desc?: string,
  activePrev: number,
  withButton?: boolean,
  modalContent?: ReactElement,
}

const ChartDescPrevMulti = memo(({chartData, active, title, measurement, desc, activePrev, withButton, modalContent}: ChartDescPrevMultiProps) => {
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);

  const showValueChange = () => Math.round((chartData[active].value - chartData[active].prev[activePrev]) * 10) / 10;

  const showShiftChange = () => (Math.round(showValueChange() / (chartData[active].prev[activePrev] / 100) * 10) / 10).toFixed(1);

  return (
    <div className="chartDescPrevMulti">
      <p className="chartDescPrevMulti__title">{title}</p>
      <b className="chartDescPrevMulti__shift">{showShiftChange()}%</b>
      <b className="chartDescPrevMulti__value">{showValueChange().toFixed(1)}</b>
      <span className="chartDescPrevMulti__measurement">{measurement}</span>
      <p className="chartDescPrevMulti__desc">{desc}</p>
      {withButton && <Button
          className='chartDescPrevMulti__button'
          onClick={() => {setIsModalOpen(true)}}>Подробнее</Button>}
      {isModalOpen && <Modal className="chartDescPrevMulti__modal" onClose={() => {setIsModalOpen(false);}}>
        {modalContent}
      </Modal>}
    </div>
  );
});

export {ChartDescPrevMulti};