import React, {useEffect, useState} from 'react';
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import { MainPage } from '../pages/MainPage';
import { Header } from './Header';
import {GasPage} from "../pages/GasPage";
import {OilPage} from "../pages/OilPage";
import {GAS_NAVIGATION, OIL_NAVIGATION} from "./Header/components/constants/config";
import { Footer } from './Footer';
import ScrollToTop from './ScrollToTop/ScrollToTop';

const AppRouter = () => {
  const [scrollY, setScrollY] = useState(0);

  const [sectionScroll, setSectionScroll] = useState(
    {
      [GAS_NAVIGATION.FIELD]: document.getElementById(GAS_NAVIGATION.FIELD)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.MINING]: document.getElementById(GAS_NAVIGATION.MINING)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.PRODUCTION]: document.getElementById(GAS_NAVIGATION.PRODUCTION)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.SUPPLY]: document.getElementById(GAS_NAVIGATION.SUPPLY)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.PRICES]: document.getElementById(GAS_NAVIGATION.PRICES)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.ASSETS]: document.getElementById(GAS_NAVIGATION.ASSETS)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.FIELD]: document.getElementById(OIL_NAVIGATION.FIELD)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.MINING]: document.getElementById(OIL_NAVIGATION.MINING)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.TECH_FIGURES]: document.getElementById(OIL_NAVIGATION.TECH_FIGURES)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.EXPORT]: document.getElementById(OIL_NAVIGATION.EXPORT)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.DEVELOPMENT]: document.getElementById(OIL_NAVIGATION.DEVELOPMENT)?.getBoundingClientRect().y || 0,
    });

  const getScrollY = () => {
    setScrollY(window.scrollY);
  }

  useEffect(() => {
    window.addEventListener('scroll', getScrollY);

    return () => {
      window.removeEventListener('scroll', getScrollY);
    }
  }, []);

  useEffect(() => {
    setSectionScroll({
      [GAS_NAVIGATION.FIELD]: document.getElementById(GAS_NAVIGATION.FIELD)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.MINING]: document.getElementById(GAS_NAVIGATION.MINING)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.PRODUCTION]: document.getElementById(GAS_NAVIGATION.PRODUCTION)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.SUPPLY]: document.getElementById(GAS_NAVIGATION.SUPPLY)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.PRICES]: document.getElementById(GAS_NAVIGATION.PRICES)?.getBoundingClientRect().y || 0,
      [GAS_NAVIGATION.ASSETS]: document.getElementById(GAS_NAVIGATION.ASSETS)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.FIELD]: document.getElementById(OIL_NAVIGATION.FIELD)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.MINING]: document.getElementById(OIL_NAVIGATION.MINING)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.TECH_FIGURES]: document.getElementById(OIL_NAVIGATION.TECH_FIGURES)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.EXPORT]: document.getElementById(OIL_NAVIGATION.EXPORT)?.getBoundingClientRect().y || 0,
      [OIL_NAVIGATION.DEVELOPMENT]: document.getElementById(OIL_NAVIGATION.DEVELOPMENT)?.getBoundingClientRect().y || 0,
    });
  }, [scrollY]);

  return (
    <Router>
      <Header
        sectionScroll={sectionScroll}
      />
      <ScrollToTop/>
      <Switch>
        <Route exact path="/">
          <MainPage/>
        </Route>
        <Route path="/oil">
          <OilPage
            sectionScroll={sectionScroll}
          />
        </Route>
        <Route path="/refining">
          <RefiningPage/>
        </Route>
        <Route path="/gas">
          <GasPage
            sectionScroll={sectionScroll}
          />
        </Route>
        <Route path="/coal">
          <CoalPage/>
        </Route>
      </Switch>
      <Footer
      />
    </Router>
  )
};

function RefiningPage() {
  return (
    <div>
      <h2>RefiningPage content</h2>
    </div>
  );
}

function CoalPage() {
  return (
    <div>
      <h2>CoalPage content</h2>
    </div>
  );
}

export default AppRouter;