import classNames from 'classnames';
import React, {useEffect, useRef, useState} from 'react';
import {NavLink, useHistory, useLocation} from "react-router-dom";
import {IconLogo} from '../../icons/logo/logo';
import {Button} from '../Elements/Button';
import {Subheader} from './components';
import {PATHS, ROUTE} from './constants';
import {IconDownload} from "../../icons/download/download";
import {disableBodyScroll, enableBodyScroll} from "body-scroll-lock";
import myPDF from '../../images/GasPages2020.jpg';
import {useDispatch} from "react-redux";
import { setCurrentPath } from '../../redux/theme.reducer';

interface HeaderProps {
  sectionScroll: {
    [key: string]: number,
  },
}

const ESC_CODE = 27;

const Header = ({sectionScroll}: HeaderProps) => {
  const [isOpened, setIsOpened] = useState<boolean>(false);

  const [isMobOpened, setIsMobOpened] = useState<boolean>(false);

  const navRef = useRef(null);

  const {pathname} = useLocation();

  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setCurrentPath(history.location.pathname));
  }, [history.location.pathname, dispatch]);

  useEffect(() => {
    setIsOpened(false);
  }, [pathname]);

  useEffect(() => {
    const escHandler = (event: any) =>{
      if (event.keyCode === ESC_CODE) {
        setIsOpened(false);
      }
    }

    document.addEventListener('keydown', escHandler, false);
    return () => {
      document.removeEventListener('keydown', escHandler, false);
    };
    });

  useEffect(() => {
    if (navRef?.current) {
      isMobOpened ? disableBodyScroll(navRef.current) : enableBodyScroll(navRef.current);
    }
  }, [isMobOpened]);

  return (
    <header className='header'>
      <div className={classNames('header__wrapper', {'home': pathname === PATHS.HOME})}/>
      <div className="header__content">
        <NavLink className='header__logo-wrapper' exact to={PATHS.HOME}>
          <IconLogo className='header__logo'/>
        </NavLink>
        <div className="header__nav-box">
          <button className={classNames("header__mobToggle", {'opened': isMobOpened})} onClick={() => {
            setIsMobOpened(!isMobOpened)
          }}>
            <span className="header__mobToggle-line"/>
          </button>
          <nav ref={navRef} className='header__nav'>
            {ROUTE.map(route => (
              <div key={route.path} className='header__nav-item-wrapper'>
                <NavLink onClick={() => {
                  setIsMobOpened(false)
                }} className='header__nav-item' to={route.path}>
                  <p className='header__nav-value'>{route.name}</p>
                </NavLink>
                {pathname === route.path && <button className={classNames({'opened': isOpened}, 'header__toggle')}
                                                    onClick={() => setIsOpened(!isOpened)}/>}
              </div>
            ))}
          </nav>
          <a className='header__btnDownload-wrapper' href={myPDF} download="GasPages2020.pdf">
            <Button
              className='header__btnDownload'
              type='gray'
              onClick={() => {
              }}>
              <IconDownload/>
              <span>Скачать отчет</span>
            </Button>
          </a>
        </div>
      </div>
      <Subheader
        className={classNames({'visible': (pathname !== PATHS.HOME) && isOpened})}
        pathname={pathname}
        sectionScroll={sectionScroll}
      />
    </header>
  )
}

export {Header};