import React, {memo, useState} from "react";
import {ClassNameElement} from "../../../../../../components/types";
import classNames from "classnames";
import {Heading} from "../../../../../../components/Elements/Heading";
import {LineChart} from "../../../../../../components/LineChart";
import { Tabs } from "../../../../../../components/Elements/Tabs";
import {CHART_DATA_BUTANE, CHART_DATA_PROPANE} from "../../../../config/prices/LineChartPricesOuter";

interface LineChartPricesOuterProps extends ClassNameElement {

}

const tooltipVals = ['2020', 'Пропан', 'Бутан'];

const LineChartPricesOuter = memo(({className}: LineChartPricesOuterProps) => {
  const [activeTab, setActiveTab] = useState<number>(0);

  return (
    <div className={classNames("lineChartPricesOuter", className)}>
      <Heading
        className='lineChartPricesOuter__title'
        value='Внешние цены (С-З Европа)'
        type='small'
      />
      <Tabs
        tabs={[{
          name: tooltipVals[1],
        },
          {
            name: tooltipVals[2],
          }]}
        active={activeTab}
        setActive={setActiveTab}
      >
        <LineChart
          activeTab={0}
          subtext='$/т'
          chartData={activeTab === 0 ? CHART_DATA_PROPANE : CHART_DATA_BUTANE}
          tooltipVals={['2020', '2019']}
          measurement='млн куб. м/сут.'
        />
      </Tabs>
    </div>
  );
});

export {LineChartPricesOuter};
