import React, {memo, ReactElement} from "react";
import {ClassNameElement} from "../types";
import classNames from "classnames";

interface PartsContentProps extends ClassNameElement{
  leftPart: ReactElement,
  rightPart?: ReactElement,
  type?: 'noLine' | 'full' | 'short',
}

const PartsContent = memo(({leftPart, rightPart, className, type}: PartsContentProps) => {
  return (
    <aside className={classNames("partsContent", className, type && type)}>
      <div className={classNames("partsContent__left", {'sole': !rightPart})}>{leftPart}</div>
      {rightPart && <div className="partsContent__right">{rightPart}</div>}
    </aside>
  );
});

export {PartsContent};