import React from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import {ClassNameElement} from "../types";
import classNames from "classnames";

interface BarChartProps extends ClassNameElement {
  option: EChartsOption,
  setActive: (index: number) => void,
  height?: string,
}

const BarChart = ({option, setActive, height, className}: BarChartProps) => {

  return (
    <div className={classNames('barChart', className)}>
      <ReactEcharts
        style={{
          height: height ? height : '300px',
        }}
        className='barChart__chart'
        option={option}
        onEvents={{
          'mouseover': (e: EChartsOption) => {
            setActive(e?.dataIndex);
          }
        }}
      />
    </div>
  )
};

export {BarChart};
