import React, {memo, ReactElement} from "react";
import {ClassNameElement} from "../../types";
import classNames from "classnames";

interface ComplexTableProps extends ClassNameElement {
  data: ComplexTableItem[],
}

export interface ComplexTableItem {
  isHeader?: boolean,
  isTop?: boolean,
  isBottom?: boolean,
  isLarge?: boolean,
  data: TableItem[],
}

interface TableItem {
  value: string | TableItem[],
  type?: string,
}

type CellRendererProps = (value: string, index: number, type?: string) => ReactElement;

const ComplexTable = memo(({className, data}: ComplexTableProps) => {
  const cellRenderer: CellRendererProps = (value, index, type) => (
    <p
      key={`${value}${index}`}
      className={classNames(
        "complexTable__cell",
        {[`complexTable__cell--${type}`]: type}
      )
      }>
      {value}
    </p>
  );

  return (
    <div className={classNames('complexTable', className)}>
      <ul className="complexTable__list">
        {data.map((item, idx) => (
          <li
            key={idx}
            className={classNames("complexTable__row",
              {'complexTable__row--header': item.isHeader},
              {'complexTable__row--large': item.isLarge},
              {'complexTable__row--top': item.isTop},
              {'complexTable__row--bottom': item.isBottom},
            )}>
            {
              item.data.map((data, i) => (
                Array.isArray(data.value) ?
                  <div
                    className="complexTable__cellWrapper"
                    key={`${i}${data}`}
                  >
                    {data.value.map((unit, idx) => {
                      if (typeof unit.value === 'string') {
                        return cellRenderer(unit.value, idx, unit.type);
                      }
                      return null;
                    })}
                  </div> :
                  cellRenderer(data.value, i, data.type)
              ))
            }
          </li>
        ))}
      </ul>
    </div>
  )
});

export {ComplexTable};