import {createSlice} from "@reduxjs/toolkit";

interface gasPageState {
  miningTab: TabOptions,
  productionTab: TabOptions,
  supplyTab: TabOptions,
  pricesTab: TabOptions,
}

enum TabOptions {
  FIRST,
  SECOND,
  THIRD,
}

const initialState: gasPageState = {
  miningTab: TabOptions.FIRST,
  productionTab: TabOptions.FIRST,
  supplyTab: TabOptions.FIRST,
  pricesTab: TabOptions.FIRST,
};

export const gasPageSlice = createSlice({
  name: 'gasPage',
  initialState,
  reducers: {
    setMiningGasTab: (state, action) => {
      state.miningTab = action.payload;
    },
    setProductionGasTab: (state, action) => {
      state.productionTab = action.payload;
    },
    setSupplyGasTab: (state, action) => {
      state.supplyTab = action.payload;
    },
    setPricesGasTab: (state, action) => {
      state.pricesTab = action.payload;
    },
  }
});


export const {setMiningGasTab, setProductionGasTab, setSupplyGasTab, setPricesGasTab} = gasPageSlice.actions;
