import React, {memo} from "react";
import {OverviewDataProps} from "../../../StructureOverview";

interface BarDescFieldProps {
  active: number,
  chartData: OverviewDataProps[],
  colors: string[],
}

const BarDescField = memo(({active, chartData, colors}: BarDescFieldProps) => {
  return (
    <div className="barDescField">
      <div className='barDescField__amountBox'>
        <p className="barDescField__amount" style={{
          color: colors[active],
        }
        }>{chartData[active].amount}</p>
        <p className="barDescField__desc">{chartData[active].desc}</p>
      </div>
        <p className="barDescField__desc barDescField__desc--long">Изменение к аналогичному периоду прошлого года</p>
      <div className='barDescField__shiftBox'>
        <p className="barDescField__shift">{chartData[active].shift}</p>
        <p className="barDescField__desc">П.П.</p>
      </div>
    </div>
  );
});

export {BarDescField};