import React from "react";
import {GasPageProps} from "../../GasPage";
import {CONFIG, GAS_NAVIGATION, HEADER_WIDTH} from "../../../../components/Header/components/constants/config";
import classNames from "classnames";
import {Tabs} from "../../../../components/Elements/Tabs";
import {Heading} from "../../../../components/Elements/Heading";
import {LineChartPricesTrade} from "./components/LineChartPricesTrade";
import {PartsContent} from "../../../../components/PartsContent";
import {LineChartPricesPlants} from "./components/LineChartPricesPlants";
import {LineChartPricesOuter} from "./components/LineChartPricesOuter";
import {LineChartPricesNear} from "./components/LineChartPricesNear";
import { PRICES_TABS } from "../../tabsConfig";
import {useSelector} from "react-redux";
import {getPricesGasTab} from "../../redux/GasPage.selector";
