import React, {memo} from "react";
import {PrevChartItemProps} from "../types";

interface ChartDescPrevProps {
  chartData: PrevChartItemProps[],
  active: number,
  title: string,
  measurement: string,
  desc?: string,
}

const ChartDescPrev = memo(({chartData, active, title, measurement, desc}: ChartDescPrevProps) => {
  const showValueChange = () => {
    return Math.round((chartData[active].value - chartData[active].prev) * 10) / 10;
  };

  const showShiftChange = () => Math.round(showValueChange() / (chartData[active].prev / 100) * 10) / 10;

  return (
    <div className="chartDescPrev">
      <p className="chartDescPrev__title">{title}</p>
      <b className="chartDescPrev__shift">{showShiftChange()}%</b>
      <b className="chartDescPrev__value">{showValueChange()}</b>
      <span className="chartDescPrev__measurement">{measurement}</span>
      <p className="chartDescPrev__desc">{desc}</p>
    </div>
  )
});

export {ChartDescPrev};