import React, {memo, PropsWithChildren} from "react";
import classNames from "classnames";
import {ClassNameElement} from "../../types";
import {scroller} from "react-scroll";
import {useDispatch} from "react-redux";

interface TabsProps extends ClassNameElement {
  tabs: TabProps[],
  active: number,
  setActive: (value: number | ActiveWithPrev) => void,
  setDispatch?: boolean,
  withPrev?: boolean,
  mode?: 'link';
  hint?: string;
  sectionClass?: string,
}

interface TabProps {
  name: string,
}

type ActiveWithPrev = (prev: number) => number;

const Tabs = memo(({tabs, active, setActive, className, withPrev = false, children, mode, hint, sectionClass, setDispatch = false}: PropsWithChildren<TabsProps>) => {
  const dispatch = useDispatch();
  return (
    <>
      <ul className={classNames("tabs", className, mode && mode)}>
        {tabs.map((tab, i) => (
          <li
            className="tabs__itemWrapper"
            key={`${tab.name}${i}`}
          >
            <button
              onClick={() => {
                if (sectionClass) {
                  scroller.scrollTo(sectionClass, {
                    duration: 0,
                    delay: 0,
                    smooth: "easeInOutQuart",
                  });
                }

                if (setDispatch) {
                  dispatch(setActive(i));
                }

                if (withPrev) {
                  setActive(prev => prev === 0 ? 1 : 0)
                } else {
                  setActive(i);
                }
              }}
              className={classNames('tabs__item', {'active': active === i})}
            >
              {tab.name}
            </button>
          </li>
        ))}
      </ul>
      {hint && <span className='tabs__hint'>{hint}</span>}
      {children}
    </>
  );
});

export {Tabs};
