import React from "react";
import {BarChartField} from "./components/BarChartField";
import {ClassNameElement} from "../types";
import classNames from "classnames";

interface StructureOverviewProps extends ClassNameElement {
  headingPart: OverviewHeadingProps,
  chartData: OverviewDataProps[],
  height: number,
}

interface OverviewHeadingProps {
  amount: number,
  value: string,
  desc: string,
}

export interface OverviewDataProps {
  amount: number,
  desc: string,
  shift: string,
  name: string,
  value: number,
  itemStyle: {
    color: string,
  }
}

const StructureOverview = ({className, chartData, headingPart, height}: StructureOverviewProps) => {
  return (
    <div className={classNames("structureOverview", className)}>
      <div className="structureOverview__upper">
        <b className="structureOverview__amount">{headingPart.amount}</b>
        <b className="structureOverview__value">{headingPart.value}</b>
        <p className="structureOverview__desc">{headingPart.desc}</p>
      </div>
      <BarChartField
        chartData={chartData}
        height={height}
      />
    </div>
  );
};

export {StructureOverview};
