import React, {memo, ReactElement, useState} from "react";
import ReactEcharts from "echarts-for-react";
import {EChartsOption} from "echarts-for-react/src/types";
import {ChartDesc} from "../ChartDesc";
import classNames from "classnames";
import {ChartDataProps, ClassNameElement} from "../types";
import {useWindowWidth} from "../../hooks/hooks";
import {useSelector} from "react-redux";
import {getCurrentPath} from "../../redux/theme.selector";

interface LineChartSoleProps extends ClassNameElement {
  chartData: ChartDataProps,
  interval?: number,
  withButton?: boolean,
  modalContent?: ReactElement,
  measurement?: string,
  subtext?: string,
  xAxisData?: string[],
}

type colorThemeType = {
  [key in string]: {
    main: string,
  }
}

// TODO: replace refining and coal colors once available
const colorTheme: colorThemeType = {
  gas: {
    main: '#1b3a98',
  },
  oil: {
    main: '#EA3742',
  },
  refining: {
    main: '#71c5bc',
  },
  coal: {
    main: '#71c5bc',
  },
}

const LineChartSole = memo(({className, chartData, interval = 160, withButton = false, modalContent, measurement, subtext, xAxisData}: LineChartSoleProps) => {
  const [active, setActive] = useState(chartData.length - 2);

  const currentPath = useSelector(getCurrentPath);

  const chartConfig = {
    title: {
      subtext: subtext || 'млрд куб. м',
    },
    height: 200,
    grid: {
      left: 40,
    },
    tooltip: {
      trigger: 'axis',
      formatter: (e: EChartsOption) => {
        return `${e[0].value}`;
      },
    },
    xAxis: {
      type: 'category',
      data: xAxisData || ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020'],
      axisTick: {
        show: false,
      },
    },
    yAxis: {
      type: 'value',
      axisLine: {
        show: false,
      },
      axisTick: {
        show: false,
      },
    },
    animationDuration: 0,
    series: [
      {
        data: chartData,
        type: 'line',
        itemStyle: {
          color: currentPath && colorTheme[currentPath].main,
        },
        selectionMode: false,
        symbol: 'circle',
        symbolSize: (value: any) => {
          if (chartData[active].value === value) {
            return 10;
          } else {
            return 0;
          }
        },
      },
    ],
  };

  const chartMobileConfig = {
    width: 320,
    height: 150,
    grid: {
      left: 40,
      top: 30,
    },
    xAxis: {
      type: 'category',
      data: ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020'],
      axisLabel: {
        fontSize: 8,
      },
      axisTick: {
        show: false,
      },
    },
    yAxis: {
      axisLine: {
        show: false,
      },
      axisLabel: {
        color: '#000',
        fontSize: 8,
      },
      minInterval: interval,
      axisTick: {
        show: false,
      },
    },
    animationDuration: 0,
    series: [
      {
        data: chartData,
        type: 'line',
        itemStyle: {
          color: currentPath && colorTheme[currentPath].main,
        },
        selectionMode: false,
        symbol: 'circle',
        symbolSize: (value: any) => {
          if (chartData[active].value === value) {
            return 10;
          } else {
            return 0;
          }
        },
      },
    ],
  };

  return (
    <section className={classNames("lineChartSole", className)}>
      <div className="lineChartSole__chart-box">
        <ReactEcharts
          className='lineChartSole__chart lineChartSole__chart--aux'
          option={useWindowWidth() < 768 ? chartMobileConfig : chartConfig}
          style={{
            height: useWindowWidth() < 768 ? '200px' : '300px',
          }}
          onEvents={{
            'highlight': (params: EChartsOption) => {
              if (params?.batch?.length) {
                setActive(params?.batch[0]?.dataIndex);
              }
            },
          }}
        />
      </div>
      <ChartDesc
        className='lineChartSole__desc'
        chartData={chartData}
        active={active}
        measurement={measurement || 'млн куб. м/сут.'}
        withButton={withButton}
        modalContent={modalContent}
      />
    </section>
  );
});

export {LineChartSole};