import React, {memo, useState} from "react";
import {ClassNameElement} from "../../../../../../components/types";
import classNames from "classnames";
import {Tabs} from "../../../../../../components/Elements/Tabs";
import {LineChart} from "../../../../../../components/LineChart";
import {CHART_DATA_BUTANE, CHART_DATA_PROPANE} from "../../../../config/prices/LineChartPricesTrade";

interface LineChartPricesTradeProps extends ClassNameElement {
}

const tooltipVals = ['2020', 'Объем продаж', 'Средневзвешенная цена'];

const LineChartPricesTrade = memo(({className}: LineChartPricesTradeProps) => {
  const [activeTab, setActiveTab] = useState<number>(0);

  return (<div className={classNames("lineChartPricesTrade", className)}>
    <Tabs
      tabs={
        [
          {
            name: tooltipVals[1],
          },
          {
            name: tooltipVals[2],
          }
        ]
      }
      active={activeTab}
      setActive={setActiveTab}
    >
      <LineChart
        activeTab={0}
        desc={'Биржевая торговля природным газом в Российской Федерации осуществляется с октября 2014 г. на базе Санкт-Петербургской Международной Товарно-сырьевой Биржи (СПбМТСБ). Биржевые договоры могут быть заключены на балансовых пунктах (БП): КС «Надым», «622,5 км (Локосово)», КС «Парабель».'}
        subtext={activeTab === 0 ? 'млн куб. м' : 'руб./тыс. куб. м'}
        chartData={activeTab === 0 ? CHART_DATA_PROPANE : CHART_DATA_BUTANE}
        tooltipVals={[]}
        measurement={activeTab === 0 ? 'млн куб. м' : 'руб./тыс. куб. м'}
      />
    </Tabs>
  </div>)
});

export {LineChartPricesTrade};
