import React, {useState} from 'react';
import classNames from 'classnames';
import {Link} from "react-router-dom";
import {Tabs} from "../../../../components/Elements/Tabs";
import {OutputItemProps} from "../../config/output";

interface OutputProps extends OutputItemProps {
  type?: 'small',
}

const Output = ({config, link, type}: OutputProps) => {
  const [order, setOrder] = useState(0);

  const isSoleItem = config.length === 1;

  const outputContent = () => (
    <>
      <div className='output__title-box'>
        {isSoleItem && (
          <h2 className='output__title'>
            {config[0].title}
          </h2>
        )}
      </div>
      <div className="output__value-box">
        <b className="output__value">{config[order].value}</b>
        <span className="output__measurement">{config[order].measurement}</span>
      </div>
      <span
        className={classNames('output__percentage', {'positive': config[order].isPositive})}>{!config[order].isPositive && '-'}{config[order].percentage}</span>
    </>
  )

  return (
    <aside className={classNames('output', type && type, {'sole': isSoleItem})}>
      {link ?
        (<Link className='output__link' to={link}>
          {outputContent()}
        </Link>) : (
          !isSoleItem ? (
              <Tabs
                className='output__tabs'
                tabs={config.map(item => ({name: item.title,}))}
                active={order}
                setActive={setOrder}
                withPrev
              >
                <button
                  className='output__tab-content'
                  onClick={() => {
                    setOrder(prev => config.length - 1 === prev ? 0 : order + 1)
                  }}
                >
                  {outputContent()}
                </button>
              </Tabs>) :
            (<div className="output__sole-content">
              {outputContent()}
            </div>))
      }
    </aside>
  );
}

export {Output};