import {createSlice} from "@reduxjs/toolkit";

enum TabOptions {
  FIRST,
  SECOND,
  THIRD,
  FOURTH,
}

interface oilPageState {
  miningTab: TabOptions,
  techFiguresTab: TabOptions,
  exportTab: TabOptions,
  techFiguresDrillingTab: TabOptions,
}

const initialState: oilPageState = {
  miningTab: TabOptions.FIRST,
  techFiguresTab: TabOptions.FIRST,
  exportTab: TabOptions.FIRST,
  techFiguresDrillingTab: TabOptions.FIRST,
};

export const oilPageSlice = createSlice({
  name: 'oilPage',
  initialState,
  reducers: {
    setMiningOilTab: (state, action) => {
      state.miningTab = action.payload;
    },
    setTechFiguresOilTab: (state, action) => {
      state.techFiguresTab = action.payload;
    },
    setExportOilTab: (state, action) => {
      state.exportTab = action.payload;
    },
    setTechFiguresDrillingOilTab: (state, action) => {
      state.techFiguresDrillingTab = action.payload;
    }
  }
});

export const {setMiningOilTab, setTechFiguresOilTab, setExportOilTab, setTechFiguresDrillingOilTab} = oilPageSlice.actions;