import React, {memo} from "react";
import classNames from "classnames";

interface SimpleTableProps {
  tableData: SimpleTableDataProps[],
  measurement?: string,
}

interface SimpleTableDataProps {
  name: string,
  value: string,
  shift?: string,
}

const SimpleTable = memo(({tableData, measurement}: SimpleTableProps) => {
  return (
    <>
      <div className="simpleTable__tableRow simpleTable__tableRow--title">
        <span/>
        <span className="simpleTable__cell simpleTable__cell--wide">2020,<br/>{measurement || 'млрд куб. м'}</span>
        <span className="simpleTable__cell">±% к 2019</span>
      </div>
      <ul className="simpleTable__tableContent">
        {tableData.map(item => (
          <li
            key={item.name}
            className="simpleTable__tableRow">
            <span className="simpleTable__cellName">{item.name}</span>
            <span className="simpleTable__cellValue">{item.value}</span>
            {item.shift ? <span
                className={classNames("simpleTable__cellShift", {'plus': item.shift[0] === '+' || item.shift[0] === '>'})}>{item.shift}</span> :
              <span className='simpleTable__cellShift simpleTable__cellShift--none'>–</span>}
          </li>
        ))}
      </ul>
      <div className="simpleTable__tableRow simpleTable__tableRow--footer">
        <span className="simpleTable__cellName">Всего</span>
        <span className="simpleTable__cellValue">77 716,4</span>
        <span className="simpleTable__cellShift">–4,1</span>
      </div>
    </>
  )
});

export {SimpleTable};